html { -moz-box-sizing: border-box; -webkit-box-sizing: border-box; box-sizing: border-box; font-size: 125%;}

*, *:before, *:after {box-sizing: inherit} * {background-color: #444;}

body {
  margin: 0;
  padding: 0;
}

h1 {
  text-align: center;
  color: #000;
  text-transform: uppercase;
  background-color: #ffffff;
  margin: 0;
  padding: 25px;
  font-size: 2.5rem;
  letter-spacing: 3px;
}

h1 span{
  background-color: #fff;
  letter-spacing: 0;
}
footer{
  background-color: #ffffff;
  width: 100%;
  padding: 25px;
  margin-top: 50px;
}

h3{
  background-color: #ffffff;
  text-align: center;
}

p{
  text-align: center;
  color: #bbb;
  padding-top: 40px;
}

.mobileInstructions{
  display: none;
}

.wrapper{
  max-width: 1200px;
  width: 80%;
  margin: 0 auto;
}

a{
  cursor: pointer;
  text-decoration: none;
  color: #000;
}

a:hover{
  color: #c02525;
}

ul {
  height: 18.875em;
  width: 548px;
  margin: 6% auto;
  padding: 60px 34px 0;
  position: relative;
  background-color: #000;
  border-radius: 22px;
}

ul div{
  height: 10px;
  background-color: #c02525;
  width: 480px;
}

li {
  margin: 0;
  padding: 0;
  list-style: none;
  position: relative;
  float: left;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 10px;

  user-select: none; /* supported by Chrome and Opera */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
}
li[tabindex]:focus {
  border: 3px solid red;
}
.whiteKey:focus {
  border: 3px solid red;
}
.whiteKey {
  height: 100%;
  width: 60px;
  z-index: 1;
  border-left: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
  border-radius: 0 0 5px 5px;
  background: linear-gradient(to bottom, #eee 0%, #fff 100%);
}

.whiteKey.playing {
  background: linear-gradient(
    to right,
    rgb(243, 243, 243) 0%,
    rgb(206, 204, 204) 100%
  );
}

.blackKey {
  height: 65%;
  width: 40px;
  margin: 0 0 0 -1em;
  z-index: 2;
  border: 1px solid #000;
  border-radius: 0 0 3px 3px;
  color: white;
  background: #444;
}

.blackKey.playing {
  background: linear-gradient(to right, #444 0%, #222 100%);
}

#D4,
#E4,
#G4,
#A4,
#B4 {
  margin: 0 0 0 -1em;
}

ul li:first-child {
  border-radius: 5px 0 5px 5px;
}

ul li:last-child {
  border-radius: 0 0 5px 5px;
}

@media (max-width: 600px){
  * {
    user-select: none; /* supported by Chrome and Opera */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
  }

  .computerInstructions{
    display: none;
  }

  .mobileInstructions{
    display: block;
    text-align: center;
    color: #eee;
    font-weight: bold;
  }
}